import styled from "styled-components";

import Link from "next/link";

const FooterDiv = styled.div`
  height: 7vh;
  padding: 0;
  margin: 0;

  footer {
    background-color: #222222;
  }

  footer .menu li a {
    color: #b1aca1;
    padding-left: 0.5vw;
    padding-right: 0.5vw;
    text-decoration: none;
  }

  footer .menu li a:hover {
    color: #7dabdb;
  }

  .fa.fa-facebook,
  .fa.fa-dropbox,
  .fa.fa-flickr,
  .fa.fa-github,
  .fa.fa-linkedin,
  .fa,
  fa-tumblr,
  .fa.fa-google-plus,
  .fa.fa-tumblr {
    color: #b1aca1;
    font-size: 1.5vw;
    padding-left: 1vw;
    padding-right: 1vw;
  }

  .fa.fa-facebook:hover,
  .fa.fa-dropbox:hover,
  .fa.fa-flickr:hover,
  .fa.fa-github:hover,
  .fa.fa-linkedin:hover,
  .fa,
  fa-tumblr:hover,
  .fa.fa-google-plus:hover,
  .fa.fa-tumblr:hover {
    color: #6794c2;
  }
`;

function Footer() {
  return (
    <FooterDiv>
      <div className="footerDiv">
        <footer>
          <div className="container">
            <div className="row text-center">
              <div className="col-md-6 col-sm-6 col-xs-12">
                <ul className="list-inline">
                  <li className="list-inline-item">
                    <Link href="https://www.facebook.com/revelermusicllc">
                      <a target="_blank">
                        <i className="fa fa-facebook fa-2x" />
                      </a>
                    </Link>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-dropbox fa-2x" />
                    </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-flickr fa-2x" />
                    </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-github fa-2x" />
                    </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-linkedin fa-2x" />
                    </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-tumblr fa-2x" />
                    </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">
                      <i className="fa fa-google-plus fa-2x" />
                    </a>
                  </li>
                </ul>
              </div>

              <div className="col-md-6 col-sm-6 col-xs-12">
                <ul className="menu list-inline">
                  <li className="list-inline-item">
                    <a href="#">Home</a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">About</a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">Blog</a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">Gallery </a>
                  </li>

                  <li className="list-inline-item">
                    <a href="#">Contact</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </footer>
      </div>
    </FooterDiv>
  );
}

export default Footer;
